import { useTranslation } from "react-i18next";
import CustomCreateButton from "../../../Ui/Button/CustomCreateButton";
import style from "../InstituteAnalytic.module.css";
import { useState } from "react";
import StaffTodayAttendanceCardList from "./StaffTodayAttendanceCardList";
import { useStaffTodayAttendaceStats } from "../../../Hooks/ApiHooks/StaffManager/staff-manager-api";

const StaffTodayAttendanceCard = ({ instituteId }) => {
  const { t } = useTranslation();
  const [step, setStep] = useState(1);

  const { staffTodayAttendaceStats } = useStaffTodayAttendaceStats({
    data: {
      id: instituteId,
    },
    skip: !instituteId,
  });

  return (
    <>
      <section className={style.stac_card}>
        <CustomCreateButton
          label={t("present")}
          customStyle={step === 1 ? preloadStyeActive : preloadStye}
          showZero
          count={staffTodayAttendaceStats?.present ?? 0}
          onAction={() => setStep(1)}
        />
        <CustomCreateButton
          label={t("absent")}
          customStyle={step === 2 ? preloadStyeActive : preloadStye}
          count={staffTodayAttendaceStats?.absent ?? 0}
          onAction={() => setStep(2)}
          showZero
        />{" "}
        <CustomCreateButton
          label={t("leave_with_pay")}
          customStyle={step === 3 ? preloadStyeActive : preloadStye}
          count={staffTodayAttendaceStats?.leave_with_pay ?? 0}
          onAction={() => setStep(3)}
          showZero
        />{" "}
        <CustomCreateButton
          label={t("on_duty_leave")}
          customStyle={step === 4 ? preloadStyeActive : preloadStye}
          count={staffTodayAttendaceStats?.on_duty_leave ?? 0}
          onAction={() => setStep(4)}
          showZero
        />
      </section>
      {step === 1 ? (
        <StaffTodayAttendanceCardList
          category="Present"
          aid={staffTodayAttendaceStats?.aid}
          emptySearch=""
        />
      ) : step === 2 ? (
        <StaffTodayAttendanceCardList
          category="Absent"
          aid={staffTodayAttendaceStats?.aid}
          emptySearch=""
        />
      ) : step === 3 ? (
        <StaffTodayAttendanceCardList
          category="Leave_with_pay"
          aid={staffTodayAttendaceStats?.aid}
          emptySearch=""
        />
      ) : step === 4 ? (
        <StaffTodayAttendanceCardList
          category="On_duty_leave"
          aid={staffTodayAttendaceStats?.aid}
          emptySearch=""
        />
      ) : null}
    </>
  );
};

export default StaffTodayAttendanceCard;

const preloadStye = {
  minWidth: "fit-content",
  margin: "0",
  "--ccb-bg": "rgba(0, 26, 114, 0.15)",
  "--ccb-b": "#001a72",
  "--ccb-text": "#001a72",
  "--ccb-text-h": "#ffffff",
  "--ccb-bg-h": "#001a72",
};
const preloadStyeActive = {
  minWidth: "fit-content",
  margin: "0",
  "--ccb-bg": "#001a72",
  "--ccb-b": "#001a72",
  "--ccb-text": "#ffffff",
  "--ccb-text-h": "#ffffff",
  "--ccb-bg-h": "#001a72",
};
