import { useTranslation } from "react-i18next";
import Loading from "../../../Loader/Loading";
import QvipleLoader from "../../../Loader/QvipleLoader";
import { assestsNavbarUrl } from "../../../Services/UrlConfig/AssestsBaseUrl";
import style from "../../../UserStaffMember/FinanceManager/FinanceManager.module.css";
import EmptyMessage from "../../../Utils/EmptyMessage/EmptyMessage";
import StaffTodayAttendanceCardListCard from "./StaffTodayAttendanceCardListCard";
import { useCallback, useEffect, useState } from "react";
import { useInView } from "react-intersection-observer";
import { useStaffTodayAttendaceStaffListStats } from "../../../Hooks/ApiHooks/StaffManager/staff-manager-api";
import { nextPage } from "../../../Utils/Functions/nextPage";
import { customDebounce } from "../../../Utils/Functions/debounce";
const StaffTodayAttendanceCardList = ({ aid, category }) => {
  const { t } = useTranslation();
  const [search, setSearch] = useState("");
  const [page, setPage] = useState(1);
  const [state, setState] = useState(true);
  const [ref, inView] = useInView({ skip: search });

  const {
    staffTodayAttendaceStaffListStats,
    staffTodayAttendaceStaffListStatsLoading,
  } = useStaffTodayAttendaceStaffListStats({
    data: {
      aid: aid,
      page: page,
      limit: 10,
      search: search,
      flow: category,
    },
    skip: aid ? !category : !aid,
  });
  useEffect(() => {
    if (inView && state) setPage((page) => page + 1);
  }, [inView, state]);
  useEffect(() => {
    if (staffTodayAttendaceStaffListStats?.list?.length) {
      let obj = nextPage(staffTodayAttendaceStaffListStats?.list?.length);
      setState(() => obj);
    }
  }, [staffTodayAttendaceStaffListStats?.list]);

  const onDebounce = useCallback(
    customDebounce((val) => {
      setPage(() => 1);
      setSearch(val);
    }, 1500),
    []
  );

  const onSearchEvent = (e) => {
    if (e.target.value) {
      if (!staffTodayAttendaceStaffListStatsLoading) onDebounce(e.target.value);
    } else {
      setPage(() => 1);
      setSearch("");
    }
  };

  return (
    <>
      <div
        className={style.fm_header_container}
        style={{
          marginTop: "1rem",
        }}
      >
        <div className={style.fm_search_full}>
          <section className={style.fm_search_full_container}>
            <div className={style.fm_search_full_container_inner}>
              <img
                className={style.fm_search_full_icon}
                alt="search icon"
                src={`${assestsNavbarUrl}/navbar-search.svg`}
              />
              <input
                type="text"
                placeholder={t("search")}
                onChange={onSearchEvent}
              />
            </div>
          </section>
        </div>
      </div>

      {staffTodayAttendaceStaffListStats?.list?.map((staff, index) =>
        staffTodayAttendaceStaffListStats?.list?.length === index + 1 ? (
          <div key={staff?._id} ref={ref}>
            <StaffTodayAttendanceCardListCard staff={staff} />
          </div>
        ) : (
          <StaffTodayAttendanceCardListCard staff={staff} key={staff?._id} />
        )
      )}
      {staffTodayAttendaceStaffListStatsLoading ? (
        search ? (
          <QvipleLoader />
        ) : (
          <Loading />
        )
      ) : search ? (
        staffTodayAttendaceStaffListStats?.list?.length === 0 && (
          <EmptyMessage
            title="No staff found related this search."
            customStyleContainer={{
              marginTop: "10rem",
            }}
          />
        )
      ) : (
        staffTodayAttendaceStaffListStats?.list?.length === 0 && (
          <EmptyMessage
            title="No staff."
            customStyleContainer={{
              marginTop: "10rem",
            }}
          />
        )
      )}
    </>
  );
};

export default StaffTodayAttendanceCardList;
